import { AlertController, LoadingController, ToastController } from 'ionic-angular';
import { Injectable } from '@angular/core';

@Injectable()
export class NoticeService {

  loader: any;

  constructor(public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

  }

  public showLoading(contentStr: string, durationTime?: number) {
    this.loader = this.loadingCtrl.create({
      content: contentStr,
      duration: durationTime
    });
    this.loader.present();
  }

  public hideLoading() {
    if (this.loader) {
      this.loader.dismiss();
    }
  }

  public showAlert(titleStr: string, subtitleStr?: string) {
    let alert = this.alertCtrl.create({
      title: titleStr,
      subTitle: subtitleStr,
      buttons: ['']
    });
    alert.present();
  }

  public showToast(msg: string, durationTime?: number) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: durationTime || 3000
    });
    toast.present();
  }

  public getAlertCtrl(): AlertController {
    return this.alertCtrl;
  }

  public getLoadingCtrl(): LoadingController {
    return this.loadingCtrl;
  }

  public getToastCtrl(): ToastController {
    return this.toastCtrl;
  }

}



