import { MatterPage } from './matter/matter';
import { Component } from '@angular/core';
import { BasePage } from "../base/base";
import { DeviceSearchPage } from "./device-search/device-search";
import { DeviceListPage } from "./device-list/device-list";
import { BindInspectPage } from "./bind-inspect/bind-inspect";
import { BasicInfoPage } from '../personal/basic-info/basic-info';
import { DeviceManagementPage } from "./device-management/device-management";
import { PersonalMaterialPage } from "../personal/personal-material/personal-material";
import { LeaveManagePage } from "../personal/leave-manage/leave-manage";
import { Constants } from '../../common/constants';

@Component({
    selector: 'page-workbench',
    templateUrl: 'workbench.html'
})
export class WorkbenchPage extends BasePage {
    basicInfo : any = JSON.parse(localStorage.getItem(Constants.PERSONALINFO));
    infodata: any;
    ionViewWillEnter() {
       // this.getBasicInfo()
    }
    getBasicInfo() {
        let date = new Date().getTime();
        this.httpService.get("user/info?timestamp=" + date).subscribe(
            data => {
                if (!data.headImage || data.headImage === null || data.headImage === '') {
                    data.headImage = 'assets/images/default_avatar.jpg';
                }
                this.infodata = data;
            },
            error => this.showToast("", 2000)
        );
    }
    items = [
        {
            icon: "assets/images/work/me-device.png",
            replace: "",
            page: DeviceListPage

        }, {
            icon: "assets/images/work/me-bind.png",
            replace: "",
            page: BindInspectPage

        }, {
            icon: "assets/images/work/me-afl.png",
            replace: "",
            page: LeaveManagePage
        }, {
            icon: "assets/images/work/me-goods.png",
            replace: "",
            page: PersonalMaterialPage
        }, {
            icon: "assets/images/work/me-work.png",
            replace: "",
            page: DeviceManagementPage
        }, {
            icon: "assets/images/work/me-search.png",
            replace: "",
            page: DeviceSearchPage
        }
    ];
    itemList = {
        icon: "list",
        name: "",
        page: DeviceListPage
    }

    itemBind = {
        icon: "link",
        name: "",
        page: BindInspectPage
    }

    itemSearch = {
        icon: "search",
        name: "",
        page: DeviceSearchPage
    }

    itemMatter = {
        icon: "bookmarks",
        name: "",
        page: MatterPage
    }
    gotoItem2() {
        this.doPush(BasicInfoPage);
    }
    gotoItem(item) {
        if (item.page == null) {
            this.showToast("", 2000);
        } else {
            this.doPush(item.page);
        }
    }
}


