import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { TabsPage } from "../../tabs/tabs";
import { Constants } from "../../../common/constants";
import { Md5 } from "ts-md5/dist/md5";

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage extends BasePage {
  loginId: any = '';
  pwd: any = '';
  pwflag: any = true;
  type: any = "password";
  showline1: any = false;
  showline2: any = false;
  ionViewWillEnter() { }

  loginPhone() {
    if (!this.loginId || this.loginId === '') {
      this.showToast("", 1000);
      return;
    }
    if (!this.pwd || this.pwd === '') {
      this.showToast("", 1000);
      return;
    }
    let user: any = { "loginId": this.loginId, "password": Md5.hashStr(this.pwd).toString() }
    this.httpService.post("user/login", user).subscribe(
      data => this.loginSuccess(data),
      error => this.showToast("", 2000)
    );
  }

  loginSuccess(data) {
    localStorage.setItem(Constants.AUTH, data.token);
    this.getBasicInfo();
   // this.pushAndRemovePage(TabsPage);
  }

  getBasicInfo() {
    let date = new Date().getTime();
    this.httpService.get("user/info?timestamp=" + date).subscribe(
      data => {this.setPersonalItem(data); this.pushAndRemovePage(TabsPage);},
      error => {this.showToast("", 2000);}
    );
  }

  setPersonalItem(data) {
    if (!data.headImage || data.headImage === null || data.headImage === '') {
      data.headImage = 'assets/images/default_avatar.jpg';
    }
    localStorage.setItem(Constants.PERSONALINFO, JSON.stringify(data));
  }
  forgetPassword() {

  }
  showpassword(flag,ps) {
    this.pwflag = flag;
    this.type = ps;
  }
  showline(key){
    if(key==1){
      this.showline1=true;
      this.showline2=false;
    }else{
      this.showline1=false;
      this.showline2=true;
    }
    let logininput:any =  document.getElementsByClassName('scroll-content')[0];
    logininput.style.paddingbottom = 0;
  }
  hideline(){
    this.showline1=false;
    this.showline2=false;
  }
} 



