import { Constants } from './../../../../common/constants';
import { Events } from 'ionic-angular';
import { NoticeService } from './../../../../common/notice.service';
import { HttpService } from './../../../../common/http.service';
import { Component } from '@angular/core';
import { NavParams, ViewController, NavController } from 'ionic-angular';

@Component({
  selector: 'page-matter-detail',
  templateUrl: 'matter-detail.html',
})
export class MatterDetailPage {

  item: any;
  isAdd: boolean = true;
  comment: string = '';
  resource: string = 'matter';
  matter: any = { 'typeId': '', 'content': '', 'houseId': '' };
  matterTypes: Array<any> = [];
  houseList: Array<any> = [];
  searchInput: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public httpService: HttpService, public noticeService: NoticeService, public events: Events) {
    this.item = navParams.data;
    if (this.item.id) {
      this.isAdd = false;
    }
    this.getMatterTypes();
  }

  dismiss() {
    this.noticeService.hideLoading();
    this.viewCtrl.dismiss();
  }

  acceptMatter() {
    if (this.comment.length == 0 || this.comment == '') {
      this.noticeService.showToast('');
      return;
    }

    this.noticeService.showLoading('...');

    let accept = {};
    accept['comment'] = this.comment;
    accept['id'] = this.item.id;

    const url = this.resource + '/accept';
    this.httpService.put(url, accept)
      .subscribe(() => {
        this.dismiss();
        this.sendRefreshEvent();
      });
  }

  addMatter(item) {
    if (item.content.length == 0 || item.content == '') {
      this.noticeService.showToast('');
      return;
    }
    if (item.houseId == '') {
      this.noticeService.showToast('');
      return;
    }
    if (item.typeId == '') {
      this.noticeService.showToast('');
      return;
    }
    this.noticeService.showLoading('...');
    this.httpService.post(this.resource, item)
      .subscribe(() => {
        this.dismiss();
        this.sendRefreshEvent();
      });
  }

  getMatterTypes() {
    const url = this.resource + '/types';
    this.httpService.get(url)
      .subscribe(res => this.matterTypes = res);
  }

  getHouseByKeywords(keywords) {
    if (!keywords) {
      return;
    }
    this.noticeService.showLoading('');
    const url = 'house';
    let qInfo = { 'keywords': keywords };
    this.httpService.get(url, qInfo)
      .finally(() => this.noticeService.hideLoading())
      .subscribe(res => {
        this.houseList = res;
      });
  }

  onInputChange(event) {
    this.getHouseByKeywords(this.searchInput);
  }

  sendRefreshEvent() {
    this.events.publish(Constants.REFRESH_MATTER_ACTION);
  }

}



