import { Observable } from 'rxjs/Observable';
import { NoticeService } from './../../../common/notice.service';
import { HttpService } from './../../../common/http.service';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-todotask',
  templateUrl: 'todotask.html',
})
export class TodotaskPage {

  msgDetail: any;
  taskObject: any;
  comment: any = {};
  isRepair = false;
  isQingjia = false;
  qingjiaComments = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, public photoViewer: PhotoViewer, public httpService: HttpService, public noticeService: NoticeService) {
    this.msgDetail = this.navParams.data;
    this.taskObject = this.msgDetail.taskObject;
    if (this.msgDetail.type == 'qingJia') {
      this.isQingjia = true;
      let qingjiaUrl = 'qingJia/' + this.taskObject.id + '/detail';
      this.httpService.get(qingjiaUrl)
        .subscribe(res => this.qingjiaComments = res.comments);
    } else {
      this.isRepair = true;
    }
  }

  handleRepair() {
    let comment = this.comment
    if (!this.comment.comment) {
      this.noticeService.showToast('');
      return;
    }
    this.noticeService.showLoading('...');
    this.assign().subscribe(
      () => {
        let command = { 'status': "IN_PROGRESS" };
        this.complete({comment,command});
      }
    );
    this.noticeService.hideLoading();
  }

  previewImg(imagePath) {
    this.photoViewer.show(imagePath);
  }

  handleQingjia(handleType: number) {
    // 1accept 0reject
    let comment = this.comment;
    if (!comment.comment) {
      this.noticeService.showToast('');
      return;
    }
    this.noticeService.showLoading('...');
    this.assign().subscribe(
      () => {
        let command = { 'review_result': handleType };
        this.complete({ comment, command });
      }
    );
    this.noticeService.hideLoading();
  }

  assign(): Observable<any> {
    let url = 'task/' + this.taskObject.id + '/assign';
    let comment = this.comment;
    return this.httpService.put(url, { 'comment': comment });
  }

  complete(requestBody) {
    let completeUrl = 'task/' + this.taskObject.id + '/complete';
    this.httpService.put(completeUrl, requestBody)
      .subscribe(() => this.navCtrl.pop());
  }

}




