import { NoticeService } from './../../../common/notice.service';
import { Constants } from './../../../common/constants';
import { MatterDetailPage } from './matter-detail/matter-detail';
import { NavController, ModalController, Events } from 'ionic-angular';
import { HttpService } from './../../../common/http.service';
import { Observable } from 'rxjs/Observable';
import { Component } from '@angular/core';

@Component({
  selector: 'page-matter',
  templateUrl: 'matter.html',
})
export class MatterPage {

  matterList: Array<any> = [];
  resource: string = 'matter';
  searchInput: string = '';
  pageNum = 1;
  pageSize = 10;
  hasMoreData = false;

  constructor(public httpService: HttpService, public navCtrl: NavController, public modalCtrl: ModalController, public event: Events, public noticeService: NoticeService) {
    event.subscribe(Constants.REFRESH_MATTER_ACTION, arg => this.ionViewDidLoad());
  }

  ionViewDidLoad() {
    this.pageNum = 1;
    this.getMatterList(1)
      .subscribe(res => this.matterList = res.data);
  }

  doRefresh(refresher) {
    this.hasMoreData = true;

    this.pageNum = 1;

    setTimeout(() => {
      refresher.complete();
    }, 3000);

    this.getMatterList(1)
      .subscribe(
      res => {
        this.matterList = res.data;
        refresher.complete();
      });

  }

  getMatterList(pageNum): Observable<any> {
    this.noticeService.showLoading('');

    let pageInfo = { 'pageNum': pageNum, 'pageSize': this.pageSize }
    return this.httpService.get(this.resource, pageInfo)
      .do(data => {
        const totalpages = Math.ceil(data.totalNumbers / this.pageSize);
        this.hasMoreData = pageNum < totalpages;
      })
      .finally(() => this.noticeService.hideLoading());
  }

  onInputChange(event) {
    this.hasMoreData = false;
    let keywords = { 'keywords': this.searchInput }
    this.httpService.get(this.resource, keywords)
      .subscribe(res => this.matterList = res.data);
  }

  onCancel(event) {
    this.searchInput = '';
    this.ionViewDidLoad();
  }

  addMatter() {
    var modal = this.modalCtrl.create(MatterDetailPage, null);
    modal.present();
  }

  itemDetail(item) {
    var modal = this.modalCtrl.create(MatterDetailPage, item);
    modal.present();
  }

  loadMore() {
    this.pageNum++;
    this.getMatterList(this.pageNum)
      .subscribe(
      res => {
        res.data.forEach(element => {
          this.matterList.push(element);
        });
      });
  }

}



