import { HttpService } from './../common/http.service';
import { Component } from '@angular/core';
import { Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginPage } from "../pages/auth/login/login";
import { Constants } from "../common/constants";
import { TabsPage } from "../pages/tabs/tabs";

@Component({
  templateUrl: 'app.html'
})
export class MyApp {

  rootPage: any = localStorage.getItem(Constants.AUTH) ? TabsPage : LoginPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, public httpService: HttpService, public event: Events) {
    platform.ready().then(() => {
      statusBar.styleLightContent(); //using light style here because we overrided toolbar style to dark colour in app.scss
      splashScreen.hide();
      this.updateTaskNum();
    });
  }

  updateTaskNum() {
    let url = 'task/todotasks';
    this.httpService.get(url)
      .flatMap(i => i)
      .filter(j => j['type'] == 'qingJia' || j['type'] == 'repair')
      .toArray()
      .subscribe(res => {
        this.event.publish(Constants.TODOTASK_NUM_ACTION, res.length == 0 ? '' : res.length);
      })
  }

}



