import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { BindInspectDetailPage } from "./bind-inspect-detail/bind-inspect-detail";

@Component({
  selector: 'page-bind-inspect',
  templateUrl: 'bind-inspect.html'
})
export class BindInspectPage extends BasePage { 
  
      approvedList : any = [];
      ionViewWillEnter() { 
          this.httpService.get("wechatUserManage/unApprovedList").subscribe(
              data => this.approvedList = data ,
              () => console.log("success")
          );
      }

      gotodetail( approve ){
         this.navCtrl.push(BindInspectDetailPage,{ "approve" : approve });
      }
}



