import { Component } from '@angular/core'; 
import { BasePage } from "../../base/base";
import { LeaveDetailPage } from "../leave-detail/leave-detail";
import { LeaveApplyPage } from "../leave-apply/leave-apply";

@Component({
  selector: 'page-leave-manage',
  templateUrl: 'leave-manage.html'
})
export class LeaveManagePage  extends BasePage  {
 
  leaveList:any = []; 
  ionViewWillEnter() {
    this.getLeaveList();
  } 
  getLeaveList () {
      this.httpService.get('qingJia').subscribe(
          data => this.success(data),
          () => console.log("success")
      );
  }
  success(data) {
     this.leaveList = data
  }

 
   getDetail(leave) {
    /* let alert = this.alertCtrl.create({
      title: leave.type_name,
      subTitle:  ""+ leave.status,
      message:    ""+ leave.num   +
                  "<br>"+ leave.endDate +
                  "<br>"+leave.startDate +
                  "<br>"+ leave.createTime +
                  "<br>:<br>"+ leave.note ,
      buttons: ['']
     });
    alert.present(); */
    this.navCtrl.push(LeaveDetailPage, { 'myParam': leave });
  }
  Qingjia(){
    this.navCtrl.push(LeaveApplyPage);
  }
}



