import { Storage } from '@ionic/storage';
import { NoticeService } from '../../common/notice.service';
import { HttpService } from '../../common/http.service';
import { Component } from '@angular/core';
import { Events } from 'ionic-angular';
import { NavController, NavParams, AlertController, ViewController, MenuController, LoadingController } from 'ionic-angular';

@Component({
  template: '<h1>BasePage</h1>',
})
export class BasePage {

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public httpService: HttpService,
    public noticeService: NoticeService,
    public storage: Storage,
    public alertCtrl: AlertController,
    public viewCtrl: ViewController,
    public menuCtrl: MenuController,
    public event: Events,
    public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
  }

  public showLoading(contentStr: string, durationTime?: number) {
    this.noticeService.showLoading(contentStr, durationTime);
  }

  public hideLoading() {
    this.noticeService.hideLoading();
  }

  public showAlert(titleStr: string, subtitleStr?: string) {
    this.noticeService.showAlert(titleStr, subtitleStr);
  }

  public showToast(msg: string, durationTime?: number) {
    this.noticeService.showToast(msg, durationTime);
  }

  public handleErr(error: any) {
    let obj = null;
    if (error) {
      obj = error.json();
    }
    this.showToast(obj.errorMessage || '');
  }

  public doAskPush(message, closeButtonText, showCloseButton, page) {
    let toast = this.noticeService.getToastCtrl().create({
      message: message,
      showCloseButton: showCloseButton,
      duration: 3000,
      closeButtonText: closeButtonText
    });

    toast.onDidDismiss((data, role) => {
      if (role == "close") {
        this.navCtrl.push(page);
      }
    });

    toast.present();
  }

  public doPush(page) {
    this.navCtrl.push(page);
  }

  public pushAndRemovePage(pushPage, removeStep?, paramObj?) {
    this.navCtrl.push(pushPage, paramObj).then(() => {
      if (removeStep) {
        for (removeStep; removeStep > 0; removeStep--) {
          console.log("removeStep=" + removeStep);
          this.navCtrl.remove(removeStep);
        }
      } else {
        const index = this.viewCtrl.index;
        this.navCtrl.remove(index);
      }
    });

  }

  loader = this.loadingCtrl.create({ content: "" });

  public openWaitLoading() {
    this.loader.present();
  }

  public closeWaitLoading() {
    this.loader.dismiss();
  }

  public openWaitLoadingTime(content, times) {
    let lodading = this.loadingCtrl.create({
      content: content ? content : "",
      duration: times ? times : 3000
    });
    lodading.present();
  }

}



