import { Component } from '@angular/core';
import { NavController , App } from 'ionic-angular';
import { LoginPage } from './../auth/login/login';
import { BasicInfoPage } from './basic-info/basic-info';
import { LeaveApplyPage } from "./leave-apply/leave-apply";
import { LeaveManagePage } from "./leave-manage/leave-manage";
import { PersonalMaterialPage } from "./personal-material/personal-material";
import { Constants } from "../../common/constants";

@Component({
  selector: 'page-personal',
  templateUrl: 'personal.html'
})
export class PersonalPage {
  basicInfo : any = JSON.parse(localStorage.getItem(Constants.PERSONALINFO));
  constructor(public navCtrl: NavController,public app: App) { }
  items = [{
            icon : "contact",
            name: "",
            entry: BasicInfoPage
        },{
            icon : "pricetag",
            name: "",
            entry: LeaveApplyPage
        },{
            icon : "pricetags",
            name: "",
            entry: LeaveManagePage
        },{
            icon : "cube",
            name: "",
            entry: PersonalMaterialPage
        },{
            icon : "md-exit",
            name: "",
            entry: null
        } ];

    gotoItem(item){
        if ( item.entry == null ) { 
            localStorage.removeItem(Constants.AUTH);
            localStorage.removeItem(Constants.PERSONALINFO); 
            this.app.getRootNav().setRoot(LoginPage);
        } else {
            this.navCtrl.push(item.entry);  
        }
    }
}



