import { MaterialDetailPage } from './material-detail/material-detail';
import { Component } from '@angular/core';
import { BasePage } from "../../base/base"; 


@Component({
  selector: 'page-personal-material',
  templateUrl: 'personal-material.html'
})
export class PersonalMaterialPage extends BasePage{

   startDate : any = new Date().toISOString(); 
   endDate : any = new Date().toISOString();
   revert:any = -1;
   materials : any = [];

   closeMenu(){
     this.menuCtrl.close();
   }

   confirmMenu () {
     if ( new Date(this.endDate).getTime() < new Date(this.startDate).getTime() ) {
          this.showToast("",1500) ;
          return;
     } 
     let param =  {
       revert: this.revert == -1 ? '' : this.revert,
       start:this.startDate.substr(0,10),
       end:this.endDate.substr(0,10)
     } 
      
      this.httpService.get('materialsOut/find',param).subscribe(
           data => this.querySuccess(data),
           error => this.queryError(),
          () => this.closeWaitLoading()
      );
    }

    querySuccess(data) { 
        this.materials = data;
        this.closeWaitLoading()
        this.menuCtrl.close();
    }

    queryError() { 
        this.closeWaitLoading()
        this.showToast("",2000) ;
    }

   changeColor( item ){ this.revert = item; }

   getDetail( material ) {
     this.navCtrl.push(MaterialDetailPage,{'materialId':material.id});
   }
}



