import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { DeviceDetailPage } from "../device-detail/device-detail";

@Component({
  selector: 'page-device-search',
  templateUrl: 'device-search.html'
})
export class DeviceSearchPage extends BasePage {
     devices : any = [];
     searchDevice( ev ) { 
        var val = ev.target.value; 
        this.httpService.get('deviceDetails?keywords='+val).subscribe(
              data => this.checkSearchData( data ),
              () => console.log("success")
          );
      }

      checkSearchData( data ) {
          this.devices = data; 
      }

      getDeviceDetail( device ) {
          this.navCtrl.push(DeviceDetailPage,{ "deviceId" : device.id });
      }
}



