import { Observable } from 'rxjs/Observable';
import { TaskdetailPage } from './taskdetail/taskdetail';
import { TodotaskPage } from './todotask/todotask';
import { Constants } from './../../common/constants';
import { Component } from '@angular/core';
import { BasePage } from "../base/base";

@Component({
  selector: 'page-task',
  templateUrl: 'task.html'
})
export class TaskPage extends BasePage {

  task: any = 'todo';
  todoList: Array<any> = [];
  doingList: Array<any> = [];
  finishList: Array<any> = [];
  todoPushPage: any = TodotaskPage;
  finishPushPage: any = TaskdetailPage;
  doingPushPage: any = TaskdetailPage;
  todoTaskNum: any = '';
  list: Array<any> = [
    { 'thing': 'todo', 'isclass': true, 'name': '' },
    { 'thing': 'doing', 'isclass': false, 'name': '' },
    { 'thing': 'finish', 'isclass': false, 'name': '' }
  ]
  ionViewWillEnter() {
    this.getTodoList();
    this.getDoingList();
    this.getFinishList();
  }

  ionViewDidLeave() {
    this.todoList = [];
    this.doingList = [];
    this.finishList = [];
    this.todoTaskNum = '';
    
  }

  getTodoList() {
    let url = 'task/todotasks';
    this.filterTaskType(this.httpService.get(url))
      .subscribe(res => {
       // console.log(this.todoList,res)
        this.todoList = this.orderCreateTimeDesc(res);
        this.todoTaskNum = this.parseBadageNum(this.todoList);
        this.event.publish(Constants.TODOTASK_NUM_ACTION, this.parseBadageNum(this.todoList));
      });
  }

  getDoingList() {
    let url = 'task/inprogresstasks';
    this.filterTaskType(this.httpService.get(url))
      .subscribe(res => this.doingList = this.orderCreateTimeDesc(res));
  }

  getFinishList() {
    let url = 'task/completedtasks';
    this.filterTaskType(this.httpService.get(url))
      .subscribe(res => this.finishList = this.orderCreateTimeDesc(res));
  }

  parseBadageNum(arr) {
    return arr.length == 0 ? '' : arr.length;
  }

  filterTaskType(observable: Observable<any>): Observable<any> {
    return observable
      .flatMap(i => i)
      .filter(j => j['type'] == 'qingJia' || j['type'] == 'repairFormDetail')
      .toArray()
  }

  onSegmentChange() {
    switch (this.task) {
      case 'todo':
        this.todoList = [];
        this.getTodoList();
        break;
      case 'doing':
        this.doingList = [];
        this.getDoingList();
        break;
      case 'finish':
        this.finishList = [];
        this.getFinishList();
        break;
    }
  }
  segemt(index, key) {
    //jquery(this).addClass("activeborder").siblings().removeClass("activeborder");
    //let that = this;
    this.list.forEach(element => {
      element.isclass = false;
    });
    this.list[index].isclass = true;
    switch (key) {
      case 'todo':
        this.todoList = [];
        this.getTodoList();
        break;
      case 'doing':
        this.doingList = [];
        this.getDoingList();
        break;
      case 'finish':
        this.finishList = [];
        this.getFinishList();
        break;
    }
  }
  orderCreateTimeDesc(comments){
		let result = []
		if(comments && comments.length > 0){
			result = comments.slice(0);
			result.sort((p,v) => {
				return new Date(v.createTime).getTime() - new Date(p.createTime).getTime();
			})
		}
		return result;
	}
}



