import { PhotoViewer } from '@ionic-native/photo-viewer';
import { HttpModule } from '@angular/http';
import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { TabsPage } from '../pages/tabs/tabs';
import { BasePage } from '../pages/base/base';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AuthModule } from "../pages/auth/auth.module";
import { TaskModule } from "../pages/task/task.module";
import { PersonalModule } from "../pages/personal/personal.module";
import { WorkbenchModule } from "../pages/workbench/workbench.module";
import { HttpService } from "../common/http.service";
import { NoticeService } from "../common/notice.service";
import { IonicStorageModule } from '@ionic/storage/es2015';

@NgModule({
  declarations: [
    MyApp,
    BasePage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicStorageModule.forRoot(),
    IonicModule.forRoot(MyApp, {
      backButtonText: ''
    }),
    AuthModule,
    TaskModule,
    PersonalModule,
    WorkbenchModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    BasePage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    HttpService,
    NoticeService,
    PhotoViewer,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})

export class AppModule { }



