import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { DeviceDetailPage } from "../device-detail/device-detail";

@Component({
  selector: 'page-device-list',
  templateUrl: 'device-list.html'
})
export class DeviceListPage extends BasePage {
    ionViewWillEnter() { 
        this.httpService.get("deviceCode").subscribe(
            data =>{this.getDeviceTreeSuccess(data);this.elsectlist = data[0].children}  ,
            () => console.log("success")
        );
    } 
    elsectlist: any = []; 
      deviceMenus : any =[]; 
   showDeviceFlag  : any = {};
   devices : any =[]; 
   getDeviceTreeSuccess( data ) {
      this.showDeviceFlag = {
          flag : this.deviceMenus.length + 1,
          id :  data[0].id,
          name :  data[0].name,
          children :  data[0].children,
      }
      this.deviceMenus.push(this.showDeviceFlag); 
   }
    
   closeMenu(){
     this.menuCtrl.close();
   }   

   addDeviceMenu(child){
      this.showDeviceFlag = {
          flag : this.deviceMenus.length + 1,
          id : child.id,
          name : child.name,
          children : child.children,
      }
      this.deviceMenus.push(this.showDeviceFlag); 
   }
   removeDeviceMenu( child ) {
      let flag =  child.flag;
      let removeList = [];
      this.deviceMenus.forEach(element => { 
          if ( flag <= element.flag ) {
              removeList.push(element);  
          }
      });
      removeList.forEach(element => { 
           this.deviceMenus.splice(this.deviceMenus.indexOf(element),1);          
      });
      this.showDeviceFlag = {
          flag : this.deviceMenus.length + 1,
          id : child.id,
          name : child.name,
          children : child.children,
      }
      this.deviceMenus.push(this.showDeviceFlag); 
   } 

   confirmMenu ( ) {
      this.httpService.get('deviceDetails?deviceCodeId='+this.showDeviceFlag.id).subscribe(
           data => this.devices= data ,
           error =>   this.menuCtrl.close(),
          () =>  this.menuCtrl.close()
      );
   }
   getDeviceDetail( device ) {
    this.navCtrl.push(DeviceDetailPage,{ "deviceId" : device.id });
   }
   switch(item){
       console.log(item)
       console.log(1)
   }
}



