import { HttpService } from './../../../common/http.service';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-taskdetail',
  templateUrl: 'taskdetail.html',
})
export class TaskdetailPage {

  msgDetail: any;
  taskObject: any;
  comments: Array<any> = [];
  isRepair = false;
  isQingjia = false;
  pageTitle = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, public photoViewer: PhotoViewer, public httpService: HttpService) {
    this.msgDetail = this.navParams.data;
    this.taskObject = this.msgDetail.taskObject;
    if (this.msgDetail.type == 'qingJia') {
      this.isQingjia = true;
      this.pageTitle = '';
      let qingjiaUrl = 'qingJia/' + this.taskObject.id + '/detail';
      this.httpService.get(qingjiaUrl)
        .subscribe(res => {this.comments =this.orderCreateTimeDesc(res.comments);});
    } else {
      this.isRepair = true;
    }

  }

  previewImg(imagePath) {
    this.photoViewer.show(imagePath);
    
  }
  orderCreateTimeDesc(comments){
		let result = []
		if(comments && comments.length > 0){
			result = comments.slice(0);
			result.sort((p,v) => {
				return new Date(v.createTime).getTime() - new Date(p.createTime).getTime();
			})
		}
		return result;
	}

}



