import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'matterTag' })
export class MatterTagPipe implements PipeTransform {

    transform(status: any, ...args: any[]) {

        if (args[0] == 'color') {
            return status == 0 ? 'danger' : 'primary';;
        }

        return status == 0 ? '' : '';

    }

}


