import { BindInspectPage } from './../bind-inspect';
import { Component } from '@angular/core';
import { BasePage } from "../../../base/base";

@Component({
    selector: 'page-bind-inspect-detail',
    templateUrl: 'bind-inspect-detail.html'
})
export class BindInspectDetailPage extends BasePage {
    showmodel: any = false;
    approve: any = {};
    blueflag: any = false;
    houseKeeperList: any = [];
    currentBindKeeper: any;
    applyKeeper: any;
    ionViewWillEnter() {
        this.approve = this.navParams.get('approve');
        this.httpService.get("wechatUserManage/getKeeperByPhone?phone=" + this.approve.phone).subscribe(
            result => {
                if (result != '') {
                    this.applyKeeper = result;
                }
            }
        )
        this.httpService.get("wechatUserManage/getKeepersByHouseId?houseId=" + this.approve.houseId).subscribe(
            data => {
                this.houseKeeperList = data; if (this.houseKeeperList.length > 0) {
                    for (let key in this.houseKeeperList) {
                        if (this.houseKeeperList[key]['isProprietor'] == 1) {
                            this.currentBindKeeper = this.houseKeeperList[key];
                        }
                    }
                }
            },
            () => console.log("success")
        );
    }

    approveAndUpdateUser(keeperId) {
        let message = "<b>" + this.approve.username + "</b>(" + this.approve.phone + ")?";
        this.showConfirm(message, 1, keeperId);
    }

    approveUser() {
        let message = "<b>" + this.approve.username + "</b>(" + this.approve.phone + ")?";
        this.showConfirm(message, 2);
    }

    rejectUser() {
        let message = "<b>" + this.approve.username + "</b>(" + this.approve.phone + ")?";
        this.showConfirm(message, 3);
    }



    showConfirm(message, flag, keeperId?) {
        let confirm = this.alertCtrl.create({
            title: '?',
            message: message,
            buttons: [
                {
                    text: '',
                    handler: () => { }
                },
                {
                    text: '',
                    handler: () => {
                        this.openWaitLoading();
                        switch (flag) {
                            case 1: this.confirmAgreeApproveAndUpdateUser(keeperId); break;
                            case 2: this.confirmAgreeApproveUser(); break;
                            case 3: this.confirmAgreeRejectUser(); break;
                            default: this.closeWaitLoading(); break;
                        }
                    }
                }
            ]
        });
        confirm.present();
    }
    wechaBasetUrl: any = 'wechatUserManage/';
    appBaseUrl: any = 'app/bing/';

    confirmAgreeApproveAndUpdateUser(keeperId) {
        let date = new Date();
        let time = date.toISOString().slice(0, 10) + " " + date.toString().slice(16,24);
        let approveTime = { approveTime: time }
        this.approve = Object.assign(this.approve, approveTime)
        if (this.approve.initType === 'APP') {
            this.httpService.put(this.appBaseUrl + "approveAndUpdate", this.approve).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        } else {
            this.httpService.put(this.wechaBasetUrl + "approveAndUpdate?oldKeeperId=" + keeperId.id, this.approve).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        }
    }
    confirmAgreeApproveUser() {
        let date = new Date();
        let time = date.toISOString().slice(0, 10) + " " + date.toString().slice(16,24);
        let approveTime = { approveTime: time }
        this.approve = Object.assign(this.approve, approveTime)
        let keeperParams = this.applyKeeper == undefined ? "" : this.applyKeeper.id;
        if (this.approve.initType === 'APP') {
            this.httpService.put(this.appBaseUrl + "approve?newKeeperId=" + keeperParams, this.approve).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        } else {
            this.httpService.put(this.wechaBasetUrl + "approve?newKeeperId=" + keeperParams, this.approve).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        }
    }


    confirmAgreeRejectUser() {
        if (this.approve.initType === 'APP') {
            this.httpService.put(this.appBaseUrl + "reject", { phone: this.approve.phone }).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        } else {
            this.httpService.put(this.wechaBasetUrl + "reject", { openId: this.approve.openId }).subscribe(
                data => this.success(),
                error => this.error(),
                () => console.log("success")
            );
        }
    }

    error() {
        this.closeWaitLoading()
        this.showToast("", 2000);
    }
    success() {
        this.closeWaitLoading();
        this.showToast("", 2000);
        this.pushAndRemovePage(BindInspectPage, 2, undefined);
    }
    showModel() {
        this.showmodel = true;
        console.log(this.approve)
        console.log(this.currentBindKeeper)
    }
    hideModel() {
        this.showmodel = false;
    }
    addblud() {
        this.blueflag = !this.blueflag;
    }
    appok() {
        this.showmodel = false;
        if (this.blueflag == true) {
            this.approveUser();
        } else {
            this.approveAndUpdateUser(this.currentBindKeeper);
        }
    }
}



