import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { Constants } from '../../../common/constants';

@Component({
  selector: 'page-device-management',
  templateUrl: 'device-management.html'
})
export class DeviceManagementPage extends BasePage {
  applyInfo: any = {};
  basicInfo: any = JSON.parse(localStorage.getItem(Constants.PERSONALINFO));
  groupid: any;
  ionViewWillEnter() {
    this.getLoginInfo();
    this.getgroupTree();
  }
  applyLeave() {
    let prompt = this.alertCtrl.create({
      title: '',
      message: "",
      inputs: [
        {
          name: 'comment',
          placeholder: ''
        },
      ],
      buttons: [
        {
          text: '',
          handler: data => {

          }
        },
        {
          text: '',
          handler: data => {
            this.apply(data, "2");
          }
        }
      ]
    });
    prompt.present();
  }

  applyRise() {
    let prompt = this.alertCtrl.create({
      title: '',
      message: "",
      inputs: [
        {
          name: 'comment',
          placeholder: ''
        },
      ],
      buttons: [
        {
          text: '',
          handler: data => {

          }
        },
        {
          text: '',
          handler: data => {
            this.apply(data, "1");
          }
        }
      ]
    });
    prompt.present();
  }
  applySyq() {
    let prompt = this.alertCtrl.create({
      title: '',
      message: "",
      inputs: [
        {
          name: 'comment',
          placeholder: ''
        },
      ],
      buttons: [
        {
          text: '',
          handler: data => {

          }
        },
        {
          text: '',
          handler: data => {
            this.apply(data, "0");
          }
        }
      ]
    });
    prompt.present();
  }
  apply(data, type) {
    let param = { comment: { comment: data.comment } };
    this.httpService.put("userApply/" + this.applyInfo.id + "/" + type + "/" + this.groupid, param).subscribe(
      data => {this.getLoginInfo();this.showToast("", 2000);},
      Error => {console.log("");this.showToast("", 2000);},
      () => console.log("")
    )
  }
  getgroupTree() {
    this.applyInfo = JSON.parse(localStorage.getItem(Constants.PERSONALINFO));
    this.httpService.get('groupTree/group/userGroups?userId=' + this.applyInfo.id).subscribe(
      data => this.groupid = data[0].id
    );
  }
  getLoginInfo() {
    this.httpService.get('user/info?timestamp=' + this.basicInfo.phone).subscribe(
      data => this.applyInfo = data
    );
  }
}


