import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { Constants } from "../../../common/constants";
import { LoginPage } from '../../auth/login/login';

@Component({
  selector: 'page-basic-info',
  templateUrl: 'basic-info.html'
})
export class BasicInfoPage extends BasePage {
   basicInfo : any = JSON.parse(localStorage.getItem(Constants.PERSONALINFO));
   applyInfo  : any = {};
   ionViewWillEnter() {
      // this.getLoginInfo();
   } 

   applyLeave( ) {
       let prompt = this.alertCtrl.create({
          title: '',
          message: "",
          inputs: [
            {
              name: 'comment',
              placeholder: ''
            },
          ],
          buttons: [
            {
              text: '',
              handler: data => {
                   
              }
            },
            {
              text: '',
              handler: data => {
              this.apply (data,"2") ;
              }
            }
          ]
        });
        prompt.present();
   }

   applyRise ( ) {
       let prompt = this.alertCtrl.create({
          title: '',
          message:"",
          inputs: [
            {
              name: 'comment',
              placeholder: ''
            },
          ],
          buttons: [
            {
              text: '',
              handler: data => {
                  
              }
            },
            {
              text: '',
              handler: data => {
                this.apply (data,"1") ;
              }
            }
          ]
        });
        prompt.present();
   }

   apply (data,type) {
      let param = {comment: {comment:data.comment}};
      this.httpService.put( "employee/" + this.applyInfo.id + "/" + type, param ).subscribe(
           data =>   this.getLoginInfo(), 
           Error =>  console.log(""), 
           () => console.log("")
      )
   }

   getLoginInfo() {
     this.httpService.get('employee/app/materialsOut/find').subscribe(
           data => this.applyInfo =  data  
      );
   }
   outPhone(){
    localStorage.removeItem(Constants.AUTH);
    localStorage.removeItem(Constants.PERSONALINFO); 
    this.doPush(LoginPage);
   }
}



