import { Component } from '@angular/core';
import { BasePage } from "../../base/base";
import { LeaveManagePage } from "../leave-manage/leave-manage";

@Component({
  selector: 'page-leave-apply',
  templateUrl: 'leave-apply.html'
})
export class LeaveApplyPage extends BasePage {

    applyType : any =''; 
    applyStart : any = new Date().toISOString(); 
    applyEnd : any = new Date().toISOString();
    applyDates : any =''; 
    applyNote : any =''; 
    leaveTypes : any =[];
    applicantsGroupId:any;
    nowDate : any = new Date().toISOString();
    startMaxDate : any = new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString(); 
    endMaxDate : any =new Date(new Date().setFullYear(new Date().getFullYear() + 2)).toISOString();
    ionViewWillEnter() {
       this.getRepairTypes (); 
    } 

    getRepairTypes () {
      this.httpService.get('qingJiaSetting').subscribe(
          data => {this.leaveTypes = data;console.log(data);
        if(data){
            this.applicantsGroupId=data[0].userId
        }
        },
          () => console.log("success")
      );
    }

    applyLeave () {
        if ( !this.applyType || this.applyType === '' ){
            this.showToast("",1500) ;
            return;
        }  
        if ( !this.applyStart || this.applyStart === '' ){
            this.showToast("",1500) ;
            return;
        }
        if ( !this.applyEnd || this.applyEnd === '' ){
            this.showToast("",1500) ;
            return;
        }
        if ( new Date(this.applyEnd).getTime() < new Date(this.applyStart).getTime() ) {
            this.showToast("",1500) ;
            return;
        } 
        if ( !this.applyDates || this.applyDates === '' || !(/^[0-9]+(\.5+)?$/.test(this.applyDates)) ){
            this.showToast("",1500) ;
            return;
        }
        if ( !this.applyNote || this.applyNote === '' ){
            this.showToast("",1500) ;
            return;
        }
        let userApply : any = {
            applicantsGroupId: this.applicantsGroupId,
          startDate : this.applyStart.substr(0,10),
          endDate: this.applyEnd.substr(0,10),
          num : this.applyDates,
          note :this.applyNote,
          type_id : this.applyType 
        };  
        this.httpService.post('qingJia',userApply).subscribe(
           data => this.applyLeaveSuccess(),
           error => this.applyLeaveError(),
        );
    }

    applyLeaveSuccess() { 
        this.showToast("",2000) ;
        this.doPush(LeaveManagePage);
    }

    applyLeaveError() { 
        this.showToast("",2000) ; 
    }
}



