import { Component } from '@angular/core';
import { BasePage } from "../../../base/base";

@Component({
  selector: 'page-material-detail',
  templateUrl: 'material-detail.html'
})

export class MaterialDetailPage extends BasePage {

    material : any = {};
    ionViewWillEnter() {
        let materialId =  this.navParams.get('materialId');
        this.getMaterialsOutById ( materialId );
    }
    
    getMaterialsOutById ( materialId ) {
        this.httpService.get("materialsOut/getMeterial/"+materialId).subscribe(
            data => this.getMaterialSuccess(data), 
            () => console.log("")
        )
    }
    getMaterialSuccess(data){
        this.material = data;
    }

    showApplyPrompt() {
        let prompt = this.alertCtrl.create({
          title: '',
          inputs: [
            {
              name: 'comment',
              placeholder: ''
            },
          ],
          buttons: [
            {
              text: '',
              handler: data => {
                  this.promptCancel();
              }
            },
            {
              text: '',
              handler: data => {
                 this.material.status = '-1';
                 this.promptApply(data);
              }
            }
          ]
        });
        prompt.present();
    }

    promptCancel () {}

    promptApply (data) { 
      let param = {comment: {comment:data.comment}};
      this.httpService.put("materialsOut/find/"+ this.material.id , param).subscribe(
           data => this.applySuccess(), 
           Error => this.material.status = '0', 
           () => console.log("")
      )
    }
    applySuccess() {
        this.getMaterialsOutById ( this.material.id ); 
    }
}



