import { Component } from '@angular/core';
import { BasePage } from "../../base/base";

@Component({
  selector: 'page-device-record',
  templateUrl: 'device-record.html'
})
export class DeviceRecordPage extends BasePage {
        deviceDetailId : any = " ";
        startDate : any = new Date().toISOString(); 
        endDate : any = new Date().toISOString();
        records : any = [];
        isAddRecord : any = true;
        repairType : any = 0; 
        repairDate : any = new Date().toISOString();
        repairLog : any = "";
        repairName : any = "";
        currentPage : any = 0;
        totalRow : any = 0;
        number : any = 3;
        queryParam : any = {};
        ionViewWillEnter() { 
            this.deviceDetailId = this.navParams.get('deviceId');
        } 
        queryRecord () { 
            if ( new Date(this.endDate).getTime() < new Date(this.startDate).getTime() ) {
                this.showToast("",1500) ;
                return;
            } 
            this.isAddRecord = false;
            this.currentPage = 0;
            this.totalRow = 0;
            let  param = {
                currentPage : this.currentPage,
                number : this.number,
                deviceDetailsId : this.deviceDetailId,
                startDate : this.startDate.substr(0,10),
                endDate : this.endDate.substr(0,10)
            }
            this.queryParam =  param;
            this.query (param);

        }
        changeDate(event){
            if ( new Date(this.endDate).getTime() < new Date(this.startDate).getTime() ) {
                this.showToast("",1500) ;
                return;
            } 
            this.isAddRecord = false;
            this.currentPage = 0;
            this.totalRow = 0;
            let  param = {
                currentPage : this.currentPage,
                number : this.number,
                deviceDetailsId : this.deviceDetailId,
                startDate : this.startDate.substr(0,10),
                endDate : this.endDate.substr(0,10)
            }
            this.queryParam =  param;
            this.query (param);
        }
        query (param) {
            this.httpService.get("deviceMaintainRecord/record",param).subscribe(
                data => this.querySuccess(data) ,
                () => console.log("success")
            );
        }
        querySuccess(data) {
            this.records = data.records ;
            this.currentPage = data.currentPage + 1 ;
            this.totalRow = data.totalRow ;
        }

        addRecord(){
            this.isAddRecord = true;
        }

        changeColor( type ){
            this.repairType  = type;
        }

        addRecordInfo(){
            if ( !this.repairName || this.repairName === '' ){
                this.showToast("",1500) ;
                return;
            }
            if ( !this.repairLog || this.repairLog === '' ){
                this.showToast("",1500) ;
                return;
            }
            this.openWaitLoading();
            let record = { 
                deviceDetailsId:this.deviceDetailId,
                maintainLog:this.repairLog,
                maintainTime:this.repairDate.substr(0,10),
                maintainType:this.repairType, 
                projectType:this.repairName
            }
            this.httpService.post("deviceMaintainRecord",record).subscribe(
                data =>  this.addRecordSuccess(data) ,
                error => this.closeWaitLoading(),
                () => console.log("success")
            );
        }
        addRecordSuccess(data) {
            this.queryRecord();
            this.repairType  = 0; 
            this.repairDate  = new Date().toISOString();
            this.repairLog  = "";
            this.repairName  = "";
            this.closeWaitLoading()
        }

        swipeLeftEvent(e) {
            if( e.direction == 2 && this.records.length != '0' ) {
                let pages = (this.totalRow / this.number) + (this.totalRow % this.number > 0 ? 1 : 0) ; 
                if ( this.currentPage  <= pages - 1 ) {
                    this.queryParam.currentPage = this.currentPage;
                    this.query (this.queryParam);
                } else {
                    this.showToast("",1500) ;
                }
              
            }
        }
        
        swipeRightEvent(e){
           if( e.direction == 4 && this.records.length != '0' ){
                if ( this.currentPage < 2) {
                    this.showToast("",1500) ;
                } else {
                    this.currentPage -= 2 ;
                    this.queryParam.currentPage = this.currentPage;
                    this.query (this.queryParam);
                }
           }
        }
}



