import { DeviceRecordPage } from './../device-record/device-record';
import { Component } from '@angular/core';
import { BasePage } from "../../base/base";

@Component({
  selector: 'page-device-detail',
  templateUrl: 'device-detail.html'
})
export class DeviceDetailPage extends BasePage {
      deviceDetail : any = {};
      parts : any = [];
      ionViewWillEnter() { 
         this.httpService.get("deviceDetails/"+this.navParams.get('deviceId')+"/info").subscribe(
              data => this.checkoutDeviceDetailParam(data) ,
              () => console.log("success")
          );
      }

      checkoutDeviceDetailParam(data){
         var regA = new RegExp("\"", "g");
         this.deviceDetail = data;
         this.deviceDetail.paramData = data.paramData.replace(regA, '').replace("{", '').replace("}", '');
         this.parts = this.deviceDetail.parts; 
      } 

      
      getDeviceRecord () {
         this.navCtrl.push(DeviceRecordPage,{ "deviceId" : this.navParams.get('deviceId') });
      }
}



