import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import { Constants } from "./constants";

@Injectable()
export class HttpService {

  private BASE_URL = Constants.BASE_URL;
  private options: RequestOptions;
  headers: any = new Headers();

  constructor(private http: Http) {
    this.headers.set('App-Version', '1.0');
  }

  getHttpHeaders() {
    let item = localStorage.getItem(Constants.AUTH);
    this.headers.set('Authorization', 'Bearer ' + item);
    this.options = new RequestOptions({ headers: this.headers });
    this.options.search = null;
  }

  /**
   * para  obj http get
   * eg : url = https://abc.xyz , para = { id : 1 }
   * requestPath = https://abc.xyz?id=1
   */
  get(url: string, para?: any): Observable<any> {
    this.getHttpHeaders();
    let params: URLSearchParams = new URLSearchParams();
    for (let key in para) {
      if (para.hasOwnProperty(key)) {
        let value = para[key];
        params.set(key, value);
      }
    }
    this.options.search = params;

    return this.http.get(this.BASE_URL + url, this.options)
      .map((res: Response) => res.json());
  }

  post(url: string, body: any): Observable<any> {
    this.getHttpHeaders();
    return this.http.post(this.BASE_URL + url, body, this.options)
      .map((res: Response) => {
        if (res.text() != '') {
          return res.json();
        }
      });
  }

  put(url: string, body: any): Observable<any> {
    this.getHttpHeaders();
    return this.http.put(this.BASE_URL + url, body, this.options)
      .map((res: Response) => {
        if (res.text() != '') {
          return res.json();
        }
      });
  }

  delete(url: string): Observable<any> {
    this.getHttpHeaders();
    return this.http.delete(this.BASE_URL + url, this.options)
      .map((res: Response) => {
        if (res.text() != '') {
          return res.json();
        }
      });
  }

}



